    <h4>flex布局</h4>
    <dm-flexbox class="flex">
      <div>普通div</div>
      <dm-flexbox-item @click="modalClick1">默认的水平flex div</dm-flexbox-item>
      <div>普通div</div>
    </dm-flexbox>
    <br />
    <dm-flexbox direction="vertical" class="flex">
      <div>普通div</div>
      <dm-flexbox-item>direction="vertical" 的垂直flex div</dm-flexbox-item>
      <div>普通div</div>
    </dm-flexbox>

    <h4>导航栏(顶部)</h4>
    <div class="navbar-wrap">
      <dm-navbar
        title="首页文字很长很长很长啊啊啊啊啊"
        bgcolor="#fff"
        color="#333"
        left-text="返回"
        left-icon="dm__icon--left"
        right-text="设置"
        @left-action="clickLeft"
        @right-action="clickRight"
      ></dm-navbar>
      <br />
      <dm-navbar
        bgcolor="#fff"
        color="#333"
        left-text=""
        left-icon="dm__icon--left"
        right-icon="dm__icon--edit"
        @left-action="clickLeft"
        @right-action="clickRight"
        ><img
          src="https://img.kancloud.cn/19/74/1974752961581c0423f189dc0e1f4fa6_201x69.png"
      /></dm-navbar>
      <dm-navbar
        bgcolor="#fff"
        color="#333"
        left-text=""
        left-icon="dm__icon--left"
        right-icon="dm__icon--edit"
        @left-action="clickLeft"
        @right-action="clickRight"
        ><div>
          <span slot="icon" class="dm__icon--loading"></span>这里是HTML
        </div></dm-navbar
      >
    </div>

    <h4>标签栏(底部)</h4>
    <div class="tabbar-wrap">
      <dm-tabbar v-model="active">
        <dm-tabbar-item
          icon="dm__icon--home"
          type="link"
          href="https://github.com/dsh225/DMUI"
          dot
          >首页</dm-tabbar-item
        >
        <dm-tabbar-item
          icon="dm__icon--mall"
          :href="hrefObj"
          badge="8"
          @click="routerAction"
          ><span slot="badge">8</span>商城</dm-tabbar-item
        >
        <dm-tabbar-item
          icon="dm__icon--add"
          href="add"
          class="add"
        ></dm-tabbar-item>
        <dm-tabbar-item
          href="###"
          img-src="https://img.kancloud.cn/19/74/1974752961581c0423f189dc0e1f4fa6_201x69.png"
          >自定义图</dm-tabbar-item
        >
        <dm-tabbar-item icon="dm__icon--my" href="my" badge="11"
          >我的</dm-tabbar-item
        >
      </dm-tabbar>
    </div>

    <h4>Tabs</h4>
    <div class="block">
      <span class="demonstration">基础的、简洁的标签页</span>
      <dm-tabs v-model="TabsActiveName" @tab-click="TabsHandleClick">
        <dm-tab-pane label="用户管理" name="first">用户管理</dm-tab-pane>
        <dm-tab-pane label="配置管理" name="second">配置管理</dm-tab-pane>
        <dm-tab-pane label="角色管理" name="third">角色管理</dm-tab-pane>
        <dm-tab-pane label="定时任务补偿" name="fourth"
          >定时任务补偿</dm-tab-pane
        >
      </dm-tabs>
    </div>
    <div class="block">
      <span class="demonstration">选项卡样式的标签页</span>
      <dm-tabs
        type="card"
        v-model="TabsActiveName2"
        @tab-click="TabsHandleClick2"
      >
        <dm-tab-pane label="用户管理" name="first">用户管理</dm-tab-pane>
        <dm-tab-pane label="配置管理" name="second">配置管理</dm-tab-pane>
        <dm-tab-pane label="角色管理" name="third">角色管理</dm-tab-pane>
        <dm-tab-pane label="定时任务补偿" name="fourth"
          >定时任务补偿</dm-tab-pane
        >
      </dm-tabs>
    </div>

    <h4>左右滑动菜单</h4>
    <div class="block">
      <span class="demonstration">请选择移动端调试模式或者真机调试</span>
      <dm-slide-menu
        v-model="slideMenuSelectedId"
        :items="slideMenuItems"
        :options="slideMenuOptions"
        @change="slideMenuHandleChange"
      >
      </dm-slide-menu>
    </div>

    <h2>弹出层</h2>
    <h4>Modal</h4>
    <dm-button type="success" @click="modalClick1">alert</dm-button>
    <dm-button type="primary" @click="modalClick2">自定义内容confirm</dm-button>
    <dm-button type="error" @click="modalClick3">特殊应用层</dm-button>
    <br />
    <dm-button type="success" @click="modalClick4">全局alert层</dm-button>
    <dm-button type="primary" @click="modalClick5"
      >全局confirm层,带回调函数</dm-button
    >
    <dm-button type="warning" @click="modalClick6"
      >全局prompt层,带回调函数</dm-button
    >

    <dm-modal
      type="alert"
      :visible="modalVisible1"
      @close="modalClose1"
      @confirm="modalOk1"
      dialog-title="提示"
      confirm-text="自定义文字"
      color="#fff"
      :z-index="zIndex"
      :mask-closable="false"
      >点击背景功能关闭</dm-modal
    >
    <dm-modal
      type="confirm"
      dialog-title="填写资料"
