.btn__block {
  margin: 0 auto !important;
}
.wrap > .dm__btn,
.dm__tag {
  margin-bottom: 10px;
}
.dm__dialog--special .dm__dialog--bd img {
  width: 100%;
  border-radius: 4px;
}
.dm__loading--wrap {
  margin: 15px auto;
}
.right-top-loading {
  position: fixed;
  right: 5px;
  top: 5px;
  z-index: 9999;
}
.right-top-loading.dm__loading--wrap {
  margin: 0;
}
.form-demo {
  width: 98%;
  margin: 0 auto;
  background: #f5f5f5;
  padding: 1%;
}
// 骨架demo
.topic-loading-item {
  margin-top: 20px;
}
.topic-loading-item .loading-item-one {
  display: flex;
  align-items: center;
}
.topic-loading-item .loading-item-one .dm__skeleton--bar {
  margin-left: 10px;
}
.topic-loading-item .loading-item-two {
  margin-top: 10px;
}
.topic-loading-item .loading-item-three {
  margin-top: 10px;
}
.dm__tabbar {
  background: #fff;
}
.tabbar-wrap,
.navbar-wrap {
  background: #f5f5f5;
  padding: 10px;
}

.dm__navbar--title img {
  height: 30px;
}
.progressView {
  > div {
    margin-bottom: 10px;
  }
}
.flex.dm__flexbox {
  border: 1px solid #eee;
  font-size: 16px;
}
.flex.dm__flexbox.dm__flex--vertical {
  border: none;
  height: 300px;
}
.flex.dm__flexbox div:nth-child(odd) {
  background: #ededed;
  width: 100px;
  height: 50px;
  align-items: center;
  justify-content: center;
  display: flex;
}

.flex.dm__flexbox.dm__flex--vertical div {
  width: 100%;
}
.flex.dm__flexbox.dm__flex--vertical .dm__flex--item {
  background: #ddd;
  align-items: center;
  justify-content: center;
  display: flex;
}
.dm__seamlessscroll--item {
  font-size: 1.5rem;
}
.selectSign {
  margin-bottom: 0;
}
.selectSign .dm__cell--item {
  padding: 5px 0 5px 10px;
}
.selectSign .dm__cell--left {
  flex: 1 !important;
  justify-content: center !important;
}
.selectSign .dm__cell--left .img {
  width: 50px;
}
.selectSign .dm__cell--right {
  flex: 4 !important;
  justify-content: flex-start !important;
  text-align: left !important;
}
.selectSign .dm__cell--right p {
  display: flex;
  align-items: center;
  margin: 1rem 0;
}
.selectSign .dm__cell--right p:last-child {
  color: #999;
  font-size: 1.4rem;
}
.selectSign .dm__cell--right p .dm__tag {
  padding: 3px 4px;
  font-size: 1.1rem !important;
  margin-left: 0.5rem;
}
.goTopView {
  position: fixed;
  right: 10px;
  bottom: 10px;
  z-index: 999999;
  cursor: pointer;
  .dm__icon--totop {
    font-size: 3.5rem;
  }
}
.dm-carousel__item h3 {
  color: #475669;
  font-size: 14px;
  opacity: 0.75;
  line-height: 150px;
  margin: 0;
}

.dm-carousel__item:nth-child(2n) {
  background-color: #99a9bf;
}

.dm-carousel__item:nth-child(2n + 1) {
  background-color: #d3dce6;
}
.dmDatePickerView {
  width: 17%;
  height: 40px;
  border-radius: 4px;
  padding-left: 40px;
  margin: 10px auto;
  line-height: 40px;
  position: relative;
  min-width: 220px;
  .iconfont {
    position: absolute;
    left: 5px;
    top: 1px;
    color: #c0c4cc;
  }
  .dm-range-editor {
    border: none;
    width: 220px;
  }
}
.demo-transition .transition-box {
  margin-bottom: 10px;
  width: 200px;
  height: 100px;
  border-radius: 4px;
  background-color: #409eff;
  text-align: center;
  color: #fff;
  padding: 40px 20px;
  margin-right: 20px;
  box-sizing: border-box;
}
</style>



